				</view>
				<view class="price-box p-md">
					<view class="price fxBox" style="align-items:flex-end">
						<view>
							<view class="text-l">
								<text>￥{{ detail.seckill_price }}</text>
							</view>
						</view>
						<view class="text-base text-sm ml-md mb-xs">
							市场价
							<text>￥{{ detail.goods_price }}</text>
						</view>
