			</view>
			<view class="action-icon" @tap="toNav('/pages/cart/cart')">
				<view class="cart-num" v-if="cartNum !== 0">{{ cartNum }}</view>
				<text class="iconfont icon-cart text-l mb-xs"></text>
				<text class="text-sm">购物车</text>
			</view>
			<view class="fxBox1" v-if="seckStatus === 1">
				<button class="goods-btn rightBtn" @click="inSeckill"><text>立即抢购</text></button>
			</view>
			<view class="fxBox1" v-else-if="seckStatus === 3">
				<button class="goods-btn rightBtn dis" :disabled="true">
					<text>抢购中...{{ goIng }}s</text>
				</button>
			</view>
			<view class="fxBox1" v-else-if="seckStatus === 4">
				<button class="goods-btn rightBtn" :disabled="true"><text>已售罄</text></button>
			</view>
			<view class="fxBox1" v-else-if="seckStatus === 5">
				<button class="goods-btn rightBtn" @click="goPay">
					<text>抢购成功，去支付</text>
					<view class="t-box" v-if="downPay"><uni-countdown :show-day="false" :second="downPay" @timeup="payTimeUp"></uni-countdown></view>
				</button>
			</view>
			<view class="fxBox1" v-else-if="seckStatus === 6">
				<button class="goods-btn rightBtn" :disabled="true"><text>已抢到</text></button>
			</view>
			<view class="fxBox1" v-else>
				<button class="goods-btn rightBtn unstart" :disabled="true"><text>未开始</text></button>
			</view>
		</view>
		<!--底部选择层-->
		<uni-popup ref="seckpop" type="center" :maskClick="false">
			<view class="seckill-poup">
				<view class="close-tit"><text class="iconfont icon-close_light text-18" @tap="closeSeckill"></text></view>
				<view v-if="goIng > 0">
					<image src="../static/images/going.svg" class="wait-img"></image>
					<view class="seck-tip">
						<text class="text-16 col-999">抢购中...{{ goIng }}s</text>
					</view>
				</view>
				<view v-else>
					<image src="../static/images/gofail.svg" class="wait-img"></image>
					<view class="seck-tip"><text class="text-16 col-999">抢购失败，商品已售罄</text></view>
				</view>
			</view>
		</uni-popup>
	</view>
</template>

<script>
import tool from '../../static/base/toolFun.js';
import goodsMock from '../../mock/goods.js';
import EmTag from '../../components/tag/tag.vue';
import TagCoupon from '../../components/tag/coupon.vue';
import redirect from '../../mixins/redirect.js';
import global from '../../mixins/global.js';
import tuiBottomPopup from '../../components/bottom-popup/bottom-popup';
import tuiNumberbox from '../../components/numberbox/numberbox';
import toHome from '../../components/header/ToHome';
import uniCountdown from '@dcloudio/uni-ui/lib/uni-count-down/uni-count-down.vue';
import uniPopup from '@dcloudio/uni-ui/lib/uni-popup/uni-popup.vue';
import mta from '../../mixins/mta';
export default {
	name: 'detail',
	mixins: [redirect, global, mta],
	components: {
		EmTag,
		TagCoupon,
		tuiBottomPopup,
		tuiNumberbox,
		toHome,
		uniCountdown,
		uniPopup
	},
	data() {
		return {
			groups: [],
			showCouponPopup: false,
			height: 64, //header高度
			top: 0, //标题图标距离顶部距离
			scrollH: 0, //滚动总高度
			popupShow: false,
			detail: {},
			carouselList: goodsMock.goodsDetail.images,
			//轮播图下标
			bannerIndex: 0,
			goodsContent: goodsMock.goodsDetail.content,
			headTab: [], //
			selectedTab: 0,
			value: 1,
			rateLists: [],
			coupon_list: [],
			sku: [],
			skuLists: [],
			skuKey: '',
			goodsSkuItem: {},
			isSeckill: false,
			isGroup: false,
			isIntegral: false,
			point: 0,
			downSecond: 0,
			downPay: 0,
			seckillInfo: [],
			couponShowInfo: [],
			btnIfLists: [],
			goods_id: 0,
			group_id: 0,
			groupsNum: 0,
			payTypeGroup: true,
			skuIndex: 0,
			cartNum: 0,
			seckParam: {},
			seckParams: {},
			seckStatus: 0, //秒杀状态 0  1 秒杀进行中  3 抢购中 4 抢购失败/售罄
			goIng: 59,
			goIngTime: null,
			tryTime: null,
			payTime: false,
			goPayFlat: false
		};
	},
	onReady() {},
	onLoad: function(options) {
		for (let k in options) {
			this.seckParam[k] = options[k];
		}
		this.seckParams = Object.assign({}, this.seckParam);
		delete this.seckParams.goods_id;
		let obj = {};
		// #ifdef MP-WEIXIN
		obj = wx.getMenuButtonBoundingClientRect();
		// #endif
		// #ifdef MP-BAIDU
		obj = swan.getMenuButtonBoundingClientRect();
		// #endif
		// #ifdef MP-ALIPAY
		my.hideAddToDesktopMenu();
		// #endif
		uni.getSystemInfo({
			success: res => {
				this.width = obj.left || res.windowWidth;
				this.height = obj.top ? obj.top + obj.height + 8 : res.statusBarHeight + 44;
				this.top = obj.top ? obj.top + (obj.height - 32) / 2 : res.statusBarHeight + 6;
				this.scrollH = res.windowWidth;
			}
		});
	},
	watch: {},
	computed: {},
	onShow() {
		if (JSON.stringify(uni.getStorageSync('seckillSuccess')) === JSON.stringify(this.seckParams)) {
			this.getDetails(true);
		} else {
			this.getDetails();
		}
		uni.showLoading({
			title: '加载中...'
		});
		const token = uni.getStorageSync('user_token');
		if (token) {
			this.getCartNum();
		} else {
			this.cartNum = 0;
		}
	},
	methods: {
		getCartNum() {
			this.$http({
				aurl: 'getCarNum',
				success: res => {
					this.cartNum = res.data.result.num;
				},
				fail: error => {
					console.log(error);
				}
			});
		},
		getTime(time) {
			let now_time = tool.toTimeStamp(tool.toFullTime()) / 1000;
			let end_time = tool.toTimeStamp(time) / 1000;
			return end_time - now_time;
		},
		couponDetail(index) {
			this.$set(this.couponShowInfo, index, !this.couponShowInfo[index]);
		},
		timeUp() {
			uni.showToast({
				title: '活动结束,下次早点来',
				icon: 'none'
			});
		},
		payTimeUp() {
			uni.showToast({
				title: '超过时间未下单',
				icon: 'none'
			});
			// 超过下单时间，重新请求
			this.payTime = true;
			uni.removeStorageSync('seckillPayTime');
			uni.removeStorageSync('seckillSuccess');
			this.getDetails();
		},
		getDetails(old) {
			this.$http({
				aurl: 'seckillDetails',
				data: this.seckParam,
				success: res => {
					if (res.data.code === 0) {
						const regex = new RegExp('<img', 'gi');
						let goods_body = res.data.result.goods_body.replace(regex, `<img style="max-width: 100%; display:block"`);
						res.data.result.goods_body = goods_body;
						this.detail = res.data.result;

						let now_time = tool.toTimeStamp(tool.toFullTime()) / 1000;
						let end_time = tool.toTimeStamp(this.detail.end_time) / 1000;
						this.downSecond = end_time - now_time;
						if (old) {
							this.seckStatus = 5;
							this.downPay = uni.getStorageSync('seckillPayTime') - tool.toTimeStamp(tool.toFullTime()) / 1000;
						} else {
							this.seckStatus = res.data.result.is_sec_kill;
						}
					}
				},
				error: err => {
					console.log(res);
				},
				complete: () => {
					uni.hideLoading();
				}
			});
		},
		inSeckill() {
			if (this.goPayFlat) {
				setTimeout(() => {
					this.goPayFlat = false;
				}, 1000);
				return;
			}
			this.goPayFlat = true;
			let token = uni.getStorageSync('user_token');
			if (!token) {
				this.toLink('/pagesA/user/bindUser');
				return;
			}
			this.$http({
				aurl: 'secKillStore',
				data: this.seckParams,
				success: res => {
					if (res.data.code === 0) {
						this.$refs.seckpop.open();
						this.seckWiat();
						this.goIngTime = setInterval(() => {
							if (this.goIng > 0) {
								this.goIng--;
							} else {
								clearInterval(this.goIngTime);
								this.seckStatus = 4;
							}
						}, 1000);

						this.tryTime = setInterval(() => {
							if (this.goIng >= 3) {
								this.seckWiat();
							} else {
								clearInterval(this.tryTime);
							}
						}, 3000);
					}
				},
				error: err => {
					console.log(err);
				}
			});
		},
		seckWiat() {
			let wait_time = this.detail.seckill_wait_time;
			this.$http({
				aurl: 'secKillWait',
				data: this.seckParams,
				success: res => {
					if (res.data.result.pass === 1) {
						this.seckStatus = 5;
						clearInterval(this.tryTime);
						clearInterval(this.goIngTime);
						//抢到购买资格，付款倒计时10分钟
						uni.setStorageSync('seckillSuccess', this.seckParams);
						uni.setStorageSync('seckillPayTime', tool.toTimeStamp(tool.toFullTime()) / 1000 + 60 * wait_time);
						if (this.payTime) {
							this.downPay = uni.getStorageSync('seckillPayTime') - tool.toTimeStamp(tool.toFullTime()) / 1000;
						}
						this.$refs.seckpop.close();
						uni.navigateTo({
							url: '/pagesA/checkout/index?isSeck=fastbuy'
						});
					}
				},
				error: err => {
					console.log(err);
				}
			});
		},
		goPay() {
			uni.navigateTo({
				url: '/pagesA/checkout/index?isSeck=fastbuy'
			});
		},
		closeSeckill() {
			this.$refs.seckpop.close();
			if (this.goIng > 0) {
				this.seckStatus = 3;
			}
		},
		bannerChange(e) {
			this.bannerIndex = e.detail.current;
		},
		change: function(e) {
			this.value = e.value;
		},
		hidePopup() {
			this.popupShow = false;
		},
		showPopup(type) {
			if (this.isGroup) {
				if (type) {
					this.goodsSkuItem = this.detail.group_spec_list[this.skuIndex];
				} else {
					this.goodsSkuItem = this.detail.spec_list[this.skuIndex];
				}
				this.payTypeGroup = type;
				this.popupShow = true;
			} else {
				this.popupShow = true;
			}
		},
		favoriteToggle(url, tip) {
			this.$http({
				aurl: url,
				data: {
					goods_id: this.detail.id,
					gm_id: this.seckParam.gm_id,
				},
				success: res => {
					this.detail.is_favorite = !this.detail.is_favorite;
					uni.showToast({
						title: tip
					});
				},
				fail: err => {}
			});
		}
	},
	destroyed() {
		clearInterval(this.goIngTime);
		clearInterval(this.tryTime);
	}
};
</script>

